.header-sticky {
  position: fixed;
  width: 90%;
  padding: 0 25px;
  left: 50%;
  transform: translateX(-50%);
  top: 25px;
  background-color: #000000f0;
  border-radius: 50px;
  box-shadow: -1px 1px 20px #fff;
  transition: opacity 3s 3s linear ;
  opacity: .9;
  z-index: 9
}

